.question-q{
    padding: .5rem;
    font-size: 1.5rem;
    text-align: center;
}

.question-form{
    display: flex;
    flex-direction: column;
    place-items: center;
    gap: .5rem;
}

.question-o-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

@media screen and (min-width: 576px){
    .question-o-container{
        width: 50%;
    }
}
